<template>
    <div>
        <van-cell-group>
            <van-field label="房屋地址：" :value="location" readonly placeholder="点击选择" @click-input="showPicker=true" :right-icon="location ? 'close' : ''" @click-right-icon="location=''" />
            <van-field label="情况描述：" value="输入框已禁用" v-model="situation" rows="4" autosize
                type="textarea"
                maxlength="50"
                placeholder="" />
            <van-field label="上传现场图片：" label-width="7.6em" :border="false">
                审判
            </van-field>
            <div style="margin-left: 4.26667vw">
                <van-uploader v-model="fileList" multiple accept="image/*" :before-read="beforeRead" :after-read="afterRead" />
            </div>
        </van-cell-group>
        <div class="submit-block"><van-button type="primary" block>提交结果</van-button></div>
        <van-popup v-model="showPicker" round position="bottom">
            <van-picker
                show-toolbar
                :columns="columns"
                @cancel="showPicker = false"
                @confirm="onConfirm"
            />
        </van-popup>
    </div>
    
</template>
<script>
import { CellGroup, Field, Uploader, Button, Popup, Picker  } from 'vant'
export default {
    name: 'repairDetail',
    components: {
        [CellGroup.name]: CellGroup,
        [Field.name]: Field,
        [Uploader.name]: Uploader,
        [Button.name]: Button,
        [Popup.name]: Popup,
        [Picker.name]: Picker
    },
    data() {
        return {
            location: '',
            situation: '',
            fileList: [],
            showPicker: false,
            columns: ['杭州', '宁波', '温州', '绍兴', '湖州', '嘉兴', '金华', '衢州']
        }
    },
    created() {
        console.log(this.$route)
    },
    methods: {
        beforeRead() {
            // params file
            // 压缩处理。。。。
            return true;
        },
        afterRead(file) {
            //uploading 表示上传中，failed 表示上传失败，done 表示上传完成
            if (Array.isArray(file)) {
                file.map(item => {
                    item.status = 'uploading';
                    item.message = '上传中...';
                    setTimeout(() => {
                        item.status = 'done';
                        item.message = '上传成功';
                    }, 1000);
                })
            } else {
                file.status = 'uploading';
                file.message = '上传中...';
                setTimeout(() => {
                    file.status = 'done';
                    file.message = '上传成功';
                }, 1000);
            }
            
        },
        onConfirm(value) {
            this.location = value;
            this.showPicker = false;
        },
    }
}
</script>
<style lang="less" scoped>
    .submit-block {
        padding: 0 5vw;
        margin-top: 5vw;
    }
</style>